import React, { useContext, useState } from "react";
import { ContextCalculator } from "../Context";

export default function KeyboardDeleteKey(props) {
  const { deleteLastPartialNumber, deleteAllPartialNumber } = useContext(
    ContextCalculator
  );
  const [startPositionDragX, setStartPositionDragX] = useState();

  const handleDragStart = (event) => {
    setStartPositionDragX(event.screenX);
  };
  const handleDragEnd = (event) => {
    event.preventDefault();
    const endPositionDragX = event.screenX;
    if (endPositionDragX - startPositionDragX < 0) deleteAllPartialNumber();
  };

  const handleClickDelete = (event) => {
    deleteLastPartialNumber();
  };

  const handleTouchStart = (event) => {
    const [touch, secondElement] = event.changedTouches;
    setStartPositionDragX(touch.screenX);
  };
  const handleTouchEnd = (event) => {
    const [touch, secondElement] = event.changedTouches;
    const endPositionDragX = touch.screenX;
    if (endPositionDragX - startPositionDragX < 0) deleteAllPartialNumber();
  };

  return (
    <div
      draggable
      onDragStart={handleDragStart}
      onDragEnd={handleDragEnd}
      onClick={handleClickDelete}
      onTouchStart={handleTouchStart}
      onTouchEnd={handleTouchEnd}
      className={"keyboard-key regular-size-key display-column-center"}
    >
      <div>{props.valueKey}</div>
    </div>
  );
}
